.settings-overlay-uniq-s1 {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(145deg, rgba(59, 42, 77, 0.5), rgba(42, 29, 58, 0.5));
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    animation: fadeIn 0.3s ease-in-out;
  }
  
  .settings-content-uniq-s1 {
    background: rgba(42, 29, 58, 0.9);
    padding: 20px;
    border-radius: 20px;
    box-shadow: 0 0 10px 5px #8a2be2;
    text-align: center;
    color: #fff;
    position: relative;
    max-width: 450px;
    width: calc(100% - 2px);
    margin: 0 1px;
    animation: slideUp 0.3s ease-in-out;
  }
  
  .close-button-uniq-s1 {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 24px;
    font-weight: bold;
    cursor: pointer;
    color: #fff;
  }
  
  .settings-title-uniq-s1 {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .settings-buttons-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .settings-button-uniq-s1 {
    font-size: 16px;
    font-weight: bold;
    padding: 10px 20px;
    margin: 10px 0;
    width: 80%;
    border: none;
    border-radius: 25px;
    background: linear-gradient(145deg, #3b2a4d, #2a1d3a);
    color: #fff;
    cursor: pointer;
    transition: background-color 0.3s, transform 0.3s, box-shadow 0.3s;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
  }
  
  .settings-button-uniq-s1:hover {
    background: linear-gradient(145deg, #2a1d3a, #3b2a4d);
    transform: scale(1.05);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.5);
  }
  
  .settings-button-uniq-s1 span {
    margin-right: 10px;
  }
  
  .app-info {
    text-align: left;
  }
  
  .app-info h3 {
    font-size: 18px;
    margin-bottom: 10px;
  }
  
  .app-info ul {
    list-style-type: none;
    padding: 0;
  }
  
  .app-info li {
    margin-bottom: 10px;
    font-size: 14px;
  }
  