.notification {
  position: fixed;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 5px 10px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  animation: fadeInOut 2s forwards;
  font-size: 12px;
  max-width: 90%;
  opacity: 0.9;
  z-index: 10000; /* Убедитесь, что уведомление сверху */
}

.notification-content {
  display: flex;
  align-items: center;
}

.notification-icon {
  margin-right: 5px;
}

.notification-icon.success {
  color: #4CAF50; /* Зеленый цвет для иконки успеха */
}

.notification-icon.error {
  color: #F44336; /* Красный цвет для иконки ошибки */
}

.notification-close {
  margin-left: 10px;
  cursor: pointer;
  color: linear-gradient(145deg, rgba(59, 42, 77, 0.8), rgba(42, 29, 58, 0.8)); 
}

@keyframes fadeInOut {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
