.case-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    color: white;
    text-align: center;
    padding-top: 20px;
  }
  
  .case-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .chest {
    width: 200px;
    height: 200px;
    cursor: pointer;
    transition: transform 0.3s ease;
  }
  
  .chest.opening {
    animation: openChest 3s forwards;
  }
  
  @keyframes openChest {
    0% { transform: scale(1); }
    50% { transform: scale(1.1); }
    100% { transform: scale(1); }
  }
  
  .case-opening-animation {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.85);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    color: white;
    text-align: center;
    padding: 20px;
    box-sizing: border-box;
  }
  
  .scroll-container {
    position: relative;
    width: 100%;
    overflow: hidden;
  }
  
  .scroll-items {
    display: flex;
    will-change: transform;
    animation-fill-mode: forwards;
    animation-timing-function: cubic-bezier(0.4, 0.0, 0.2, 1);
  }
  
  .scroll-item {
    width: 100px;
    height: 100px;
    margin-right: 10px;
  }
  
  .cursor {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 10px;
    height: 100px;
    background-color: red;
    z-index: 1001;
  }
  
  .reward {
    margin-top: 20px;
  }
  
  .reward img {
    width: 100px;
    height: 100px;
    margin-bottom: 10px;
  }
  
  .inventory-button {
    background-color: rgba(0, 0, 0, 0.85);
    color: #fff;
    padding: 10px 20px;
    border-radius: 10px;
    border: none;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
    margin-top: 20px;
    position: relative;
  }
  
  .inventory-button::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 10px;
    padding: 2px;
    background: linear-gradient(135deg, #FFD700, transparent);
    -webkit-mask: 
      linear-gradient(#fff 0 0) content-box, 
      linear-gradient(#fff 0 0);
    -webkit-mask-composite: destination-out;
    mask-composite: exclude;
  }
  